export default {
    data() {
            return {
				PageCur: 'Home',
            }
        },
    methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
    }
}
